.glossaryList{
  padding: 15px;
  &__cat{
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
  &__catTitle{
    font-size: 1.15rem;
    margin-bottom: 10px;
    border-bottom: 1px dotted #ccc;
    font-weight: bold;
  }
  &__wordList{

  }
  &__word{
    &:not(:last-child){
      margin-bottom: 3px;
    }
    a{
      font-size: 0.9rem;
      line-height: 1.3;
      color: inherit;
      position: relative;
      padding-left: 15px;
      display: inline-block;
      &::before{
        content: '';
        position: absolute;
        top: 3px;
        left: 3px;
        width: 0;
        height: 0;
        border: 3px solid transparent;
        border-left-width: 5px;
        border-left-style: solid;
        opacity: 0.7;
      }
    }
  }
  @media all and (min-width: $breakpoint + 1){
    padding: 15px 30px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &::after{
      content:"";
      display: block;
      width:30%;
    }
    &__cat{
      width: 30%;
      margin-bottom: 30px;
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
    &__word{
      a{
        @include hover;
      }
    }
  }
}

.glossaryNavi{
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  &__item{
    width: 25%;
    padding: 0 3px;
    margin-bottom: 6px;
    &:last-child{
      width: 50%;
    }
    a{
      display: block;
      padding: 5px;
      border-radius: 4px;
      border: 1px solid #ccc;
      line-height: 1.2;
      font-weight: bold;
      font-size: 11px;
      background-image: linear-gradient(#ffffff, #dfdfdf);
      position: relative;
      padding-left: 20px;
      &::after{
        content: '';
        position: absolute;
        left: 4px;
        top: 50%;
        transform: translateY(-50%);
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background-color: #ccc;
        z-index: 1;
      }
      &::before{
        content: '';
        position: absolute;
        left: 4px;
        top: 50%;
        transform: translate(4px,-1px);
        width: 0;
        height: 0;
        border: 3px solid transparent;
        border-top: 4px solid #fff;
        z-index: 2;
      }
    }
  }
  @media all and (min-width: $breakpoint + 1){
    padding: 15px 30px;
    &__item{
      // flex: 1;
      // width: auto;
      width: 16.66%;
      &:last-child{
        width: 33.33%;
      }
      a{
        font-size: 1rem;
        padding: 8px 8px 8px 20px;
        @include hover;
      }
    }

  }
}

.glossaryDetail{
  &__container{
    padding: 15px;
  }
  &__title{
    font-size: 1.15rem;
    line-height: 1.3;
    font-weight: 700;
    margin-bottom: 10px;
    border-bottom: 1px dotted #999;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }
  &__back{
    margin-top: 30px;
    text-align: center;
  }
  @media all and (min-width: $breakpoint + 1){
    &__container{
      padding: 15px 30px 30px;
    }
    &__title{
      font-size: 20px;
      margin-bottom: 15px;
    }

  }
}
